 насчитывает свыше 50 наименований сухих строительных смесей для наружных и внутренних работ. Рецептуры смесей разрабатываются в собственных производственных лабораториях, оснащенных лучшим современным оборудованием.")]),_c('p',{staticClass:"MsoNormal"},[_vm._v("Сергей Юрьевич Зюзя, генеральный директор ООО «Зика», ООО «Крепс»: «От лица компании Sika с большим удовольствием приветствую сотрудников Крепс в нашей большой и дружной команде!"),_c('br'),_vm._v(" Объединение компаний Sika и Крепс дает нам платформу для дальнейшего роста и укрепления наших позиций на строительном рынке. Наша главная цель – не просто объединение возможностей Sika и Крепс, а качественный рывок в развитии!»")])])]),_c('div',{staticClass:"textimage section"},[_c('div',{staticClass:"cmp-textimage cmp-textimage__list-bullet"},[_c('div',{staticClass:"cmp-image-- cmp-image"},[_c('picture',{attrs:{"data-image-src":"{\"isResponsive\":true,\"pattern\":\"https://sika.scene7.com/is/image/sika/ru-company-profile-08${imageProfile}?fit\\u003dcrop%2C1\\u0026wid\\u003d${width}\\u0026hei\\u003d${height}\\u0026fmt\\u003d${format}\",\"originalWidth\":770,\"originalHeight\":513,\"imageProfileNamePrefix\":\":\",\"imageProfileMap\":{}}","data-height-based":"false"}},[_c('source',{attrs:{"type":"image/webp","srcset":"https://sika.scene7.com/is/image/sika/ru-company-profile-08?fit=crop%2C1&wid=770&hei=513&fmt=webp-alpha" + " 1x"}}),_c('source',{attrs:{"type":"image/png","srcset":"https://sika.scene7.com/is/image/sika/ru-company-profile-08?fit=crop%2C1&wid=770&hei=513&fmt=png-alpha" + " 1x"}}),_c('img',{staticClass:"cmp-image__image",attrs:{"src":"https://sika.scene7.com/is/image/sika/ru-company-profile-08?fit=crop%2C1&wid=770&hei=513&fmt="}})])])])])]),_c('div',{staticClass:"cell small-12 medium-4 large-4"},[_c('div',{staticClass:"layoutcontainer cmp-background--grey section"},[_c('div',[_c('div',{staticClass:"cmp-layout grid-container"},[_c('div',{staticClass:"cmp-layout__content grid-x grid-margin-x"},[_c('div',{